import { createApp } from 'vue'
import { createRouter, createWebHistory } from "vue-router";
import App from './App.vue'
import './index.css'
import './stars.css'
import 'animate.css'

import Home from './pages/MainPage.vue'
import About from './pages/AboutMe.vue'
import Projects from './pages/MyProjects.vue'
import Contact from './pages/ContactMe.vue'

import RuHome from './pages/ruVersion/MainPage.vue'
import RuAbout from './pages/ruVersion/AboutMe.vue'
import RuProjects from './pages/ruVersion/MyProjects.vue'
import RuContact from './pages/ruVersion/ContactMe.vue'

// import NotFound from './pages/NotFound.vue'

const routes = [
  { path: '/', redirect: '/ru' },
  { path: '/ru', component: RuHome },
  { path: '/ru/aboutme', component: RuAbout },
  { path: '/ru/projects', component: RuProjects },
  { path: '/ru/contactme', component: RuContact },

  { path: '/en', component: Home },
  { path: '/en/aboutme', component: About },
  { path: '/en/projects', component: Projects },
  { path: '/en/contactme', component: Contact },
]


const router = createRouter({
  history: createWebHistory(),
  routes,
})

const app = createApp(App)
app.use(router)
app.mount('#app')
